export default {
  props: {
    placeholder: {
      type: String,
      default: '搜索歌手、歌曲'
    }
  },
  data() {
    return {
      queryKey: ''
    }
  },
  methods: {
    setQueryKey(queryKey) {
      this.queryKey = queryKey
    },
    clear() {
      this.queryKey = ''
    }
  },
  created() {
    this.$watch('queryKey', (newVal) => {
      this.$emit('onChangeQuery', newVal)
    })
  }
}



