export default {
  props: {
    songs: {
      type: Array,
      default: []
    },
    rank: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    getRankText(index) {
      if (index > 2) {
        return index + 1
      }
    },
    getRankCls(index) {
      if (index <= 2) {
        return 'icon icon' + index
      } else {
        return 'text'
      }
    },
    _getDesc(song) {
      return `${song.singer} - ${song.album}`
    },
    click(item, index) {
      this.$emit('select', item, index)
    }
  }
}



